<template>
  <div class="home">
    <van-swipe class="home-swipe" :autoplay="3000">
      <van-swipe-item v-for="(carousel, index) in carousels" :key="index" @click="clickSwipe(carousel.carouselUrl)">
        <img :src="carousel.carouselName" />
      </van-swipe-item>
    </van-swipe>

    <div class="welcome">{{ timeText }}好，欢迎定制您的专属产品手册</div>

    <div class="start">
      <van-button type="default" color="#ffa500" class="b-start" round @click="customize">
        <div class="icon-c"><van-icon class="iconfont" class-prefix='icon' name="dingzhiyewushenqing" /></div>
        <div class="t-start"><div class="div1">开 始 定 制</div><div class="div2">Customize Now</div></div>
      </van-button>
    </div>

    <van-goods-action>
      <van-goods-action-icon>
        <van-icon slot="icon" class="iconfont" class-prefix='icon' name="shouye" />
        首页
      </van-goods-action-icon>

      <van-goods-action-icon>
        <van-icon slot="icon" class="iconfont" class-prefix='icon' name="wode"  @click="goMine"/>
        我的
      </van-goods-action-icon>
    </van-goods-action>
  </div>
</template>

<script>

import { Swipe, SwipeItem, Button, Icon, GoodsAction, GoodsActionIcon, GoodsActionButton, Row, Col } from 'vant';
import { getNews, getCarousel } from '@/api/news'
import { getUserInfo } from '@/api/user'
import { BASE_API } from '@/constant'
import { mapGetters } from 'vuex';

export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Button.name]: Button,
    [Icon.name]: Icon,
    [Row.name]: Row,
    [Col.name]: Col,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton
  },
  data() {
    return {
      carousels: [],
      news: [],
      timeText: ''
    };
  },

  computed: {
    // 使用对象展开运算符将 getter 混入 computed 对象中
    ...mapGetters([
      'token'
    ]),

    user:{
      get(){
        return this.$store.state.app.user
      },
      set(val){
        this.$store.commit('app/setUser', val)
      }
    },
  },

  created() {
    const str = window.location.href.split('token=')[1];
    if (str) {
      const token = str.split('#/')[0];
      this.$store.dispatch('app/setToken', token);
      this.getData()
    }
    const hour = new Date().getHours();
    if (hour >= 6 && hour < 12) {
      this.timeText = "早上"
    } else if (hour >= 12 && hour < 18) {
      this.timeText = "下午"
    } else {
      this.timeText = "晚上"
    }
  },

  methods: {
    goMine() {
      this.$router.push({path: '/mine'})
    },
    customize(){
      this.$router.push('/customise');
    },
    clickSwipe (url) {
        window.open(url, '_blank');
    },
    clickNews(url) {
      window.open(url, '_blank');
    },
    getData() {
      getNews().then( res => {
        if (res.status === 200) {
          this.news = res.data;
          this.news.forEach( value => {
            return value.newsName = BASE_API + value.newsName
          })
        }
      })

      getCarousel().then( res => {
        if (res.status === 200) {
          this.carousels = res.data;
          this.carousels.forEach( value => {
            return value.carouselName = BASE_API + value.carouselName
          })
        }
      }),

      getUserInfo().then (res => {
        if (res.status === 200) {
          this.user = res.data;
        }
      })
    }
  }
};
</script>

<style lang="less" scoped>
.home {
  padding-bottom: 50px;

  &-swipe {
    img {
      width: 100%;
      display: block;
      padding-left: 2.66667vw;
      padding-right: 2.66667vw;
    }
  }

  .welcome {
    text-align: center;
    margin: 4vh 0;
    color: #575656;
  }
  .start {
    padding-left: 10px;
    padding-right: 10px;
  }

  .b-start {
    width: 100%;
    box-shadow: 0px 8px 25px #96979969;
    .icon-c {
      color: #101010;
      display: inline-block;
      position: relative;
      i {
        position: absolute;
        top: -33px;
        left: -10px;
      }
    }
    .t-start {
      color: #101010;
      line-height: 0;
      display: inline-block;
      margin-left: 10px;
      height: 100%;
      .div1 {
        height: 50%;
        line-height: 33px;
        font-size: 16px;
        font-weight: bold;
      }
      .div2 {
        height: 50%;
        font-size: 3vw;
        line-height: 19px;
      }
    }
  }

  .news {
    .news-title {
      margin-top: 10.33333vw;
      margin-bottom: 5.6vw;
    }

    padding-left: 10px;
    padding-right: 10px;
    .news-d {
      display: inline-block;
      width: 50%;
      padding: 5px;
      box-shadow: 3px 3px 10px #9697996b;
    }
    .news-d:nth-child(2n+1) {
      padding-left: 10px;
    }
    .news-d:nth-child(2n) {
      padding-right: 10px;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  /deep/.van-goods-action {
    box-shadow: 0px -10px 54px #9697999c;
    border-top-left-radius: 17px;
    border-top-right-radius: 17px;
    height: 16.33333vw;
    .van-goods-action-icon {
      border-top-left-radius: 17px;
      border-top-right-radius: 17px;
      width: 25%;
      color: #ffa500;
      .iconfont {
        color: #ffa500;
        font-size: 7.26667vw;
      }
      .van-goods-action-icon__icon {
        width: 2em;
      }
    }
  }

}

</style>
